import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { scirtsSelector } from '../../selectors';

import ScirtContainer from './ScirtContainer';

export class ScirtsListContainer extends Component {
    render() {
        const { scirts } = this.props;

        return scirts.map(scirt => (
            <ScirtContainer
                key={scirt.id}
                id={scirt.id}
                isSelected={scirt.isSelected}
                image={scirt.image}
            />
        ));
    }
}

ScirtsListContainer.propTypes = {
    scirts: PropTypes.array.isRequired,
};

export default connect(state => ({
    scirts: scirtsSelector(state),
}))(ScirtsListContainer);



