import { CHECK_GAME, RESET_GAME, SET_SCORE } from '../constants';

const initialState = {
    isFinished: false,
    score: null,
};

const gameState = (state = initialState, action) => {
    switch (action.type) {
        case CHECK_GAME:
            return action.payload.every(card => card.isOut)
                ? { ...state, isFinished: true }
                : state;
        case RESET_GAME:
            return { ...state, isFinished: false, score: null };
        case SET_SCORE:
            const time = action.payload.split(':');
            const minutes = +time[0];
            const seconds = +time[1];
            
            return { ...state, score: 60 * minutes + seconds };
        default:
            return state;
    }
};

export default gameState;



