import React from 'react';

import ScirtsListContainer from '../container/ScirtsListContainer';

import './../../styles/GameSetting.css';

const GameSetting = () => {
    return (
        <section className="game-setting">
            <h2>Game Setting</h2>
            <figure className="skirt-cards">
                <figcaption>Select Scirt:</figcaption>
                <div>
                    <ScirtsListContainer />
                </div>
            </figure>
        </section>
    );
};

export default GameSetting;



