import { call, put, select } from 'redux-saga/effects';
import axios from 'axios';

import { getScore } from './../selectors';

import {
    SET_SCORE,
    SEND_DATA_ERROR,
    FETCH_RECORDS_SUCCESS,
    FETCH_RECORDS_ERROR,
    FETCH_RECORDS_REQUEST,
} from '../constants';

export function* setScore(action) {
    yield put({
        type: SET_SCORE,
        payload: action.payload.time,
    });
}

export function* sendData(action) { 
    const score = yield select(getScore);
    try {
        yield call(axios, 'https://mmg-score.herokuapp.com/', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            data: {
                name: action.payload.name,
                email: action.payload.email,
                score: score
            },
        });
    } catch (error) {
        yield put({
            type: SEND_DATA_ERROR,
            payload: error,
        });
    }
};

export function* fetchRecords() { 
    try {
        yield put({
            type: FETCH_RECORDS_REQUEST,
        });

        const response = yield call(axios, 'https://mmg-score.herokuapp.com/', {
            method: 'GET',
        });
        
        const sortedResults = response.data.result.sort((a, b) => a.score - b.score);

        yield put({
            type: FETCH_RECORDS_SUCCESS,
            payload: sortedResults,
        });
    } catch (error) { 
        yield put({
            type: FETCH_RECORDS_ERROR,
            payload: error,
        });
    }
};

export function* fetchSortedResults(action) {
    yield setScore(action);
    yield sendData(action);
    yield fetchRecords();
}


