import React from 'react';
import PropTypes from 'prop-types';

import './../../styles/Timer.css';

const Timer = ({ time }) => {
    return (
        <section className="timer">
            <span>{time}</span>
        </section>
    );
};

Timer.propTypes = {
    time: PropTypes.string,
};

export default Timer;



