import React, { Component, Fragment } from 'react';
import { Route, Switch } from 'react-router-dom';
import { hot } from 'react-hot-loader';

import IntroductionContainer from './IntroductionContainer';
import GamePageContainer from './GamePageContainer';
import ScorePageContainer from './ScorePageContainer';

export class AppContainer extends Component {
    render() {
        return (
            <Fragment>
                <Switch>
                    <Route exact path="/" component={IntroductionContainer} />
                    <Route path="/game" component={GamePageContainer} />
                    <Route path="/results" component={ScorePageContainer} />
                </Switch>
            </Fragment>
        );
    }
}

export default hot(module)(AppContainer);



