import { uniqueId } from 'lodash';

import { SELECT_SCIRT } from '../constants';

import skirt1 from './../assets/images/skirt1.png';
import skirt2 from './../assets/images/skirt2.png';
import skirt3 from './../assets/images/skirt3.png';

const initialState = [
    {
        id: uniqueId(),
        image: skirt1,
        isSelected: true,
    },
    {
        id: uniqueId(),
        image: skirt2,
        isSelected: false,
    },
    {
        id: uniqueId(),
        image: skirt3,
        isSelected: false,
    },
];

const scirts = (state = initialState, action) => {
    switch (action.type) {
        case SELECT_SCIRT:
            const selectedScict = state.find(
                scirt => scirt.id === action.payload
            );

            return selectedScict.isSelected
                ? state
                : state.map(scirt => {
                      if (scirt.isSelected || scirt.id === action.payload) {
                          scirt.isSelected = !scirt.isSelected;
                      }
                      return scirt;
                  });
        default:
            return state;
    }
};

export default scirts;



