import {
    CHANGE_TIME,
    START_TIMER,
    STOP_TIMER,
    RESET_TIMER,
} from '../constants';

const initialState = {
    time: null,
    id: null,
};

const ticTac = time => {
    let mm = time.split(':')[0];
    let ss = time.split(':')[1];

    ++ss;

    if (ss > 59) {
        ++mm;
        mm < 10 && (mm = '0' + mm);
        ss = 0;
    }

    ss < 10 && (ss = '0' + ss);

    return `${mm}:${ss}`;
};

const timer = (state = initialState, action) => {
    switch (action.type) {
        case CHANGE_TIME:
            return {
                ...state,
                time: ticTac(state.time),
            };
        case START_TIMER:
            return {
                ...state,
                time: '00:00',
                id: setInterval(() => action.payload(), 1000),
            };
        case STOP_TIMER:
            clearInterval(action.payload);

            return {
                ...state,
                id: null,
            };
        case RESET_TIMER:
            return {
                ...state,
                time: '00:00',
            };
        default:
            return state;
    }
};

export default timer;



