import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { resetTimer, resetGame } from '../../actions';

import ScorePageButtons from '../presentational/ScorePageButtons';

export class ScorePageButtonsContainer extends Component {
    handleClickTryBtn = () => {
        this.props.resetGame();
        this.props.history.push('/game');
    };

    handleClickMenuBtn = () => {
        this.props.resetGame();
        this.props.history.push('/');
    };

    render() {
        return (
            <ScorePageButtons
                handleClickTryBtn={this.handleClickTryBtn}
                handleClickMenuBtn={this.handleClickMenuBtn}
            />
        );
    }
}

ScorePageButtonsContainer.propTypes = {
    resetTimer: PropTypes.func.isRequired,
    resetGame: PropTypes.func.isRequired,
};

export default withRouter(
    connect(
        null,
        {
            resetTimer,
            resetGame,
        }
    )(ScorePageButtonsContainer)
);



