import React from 'react';
import PropTypes from 'prop-types';

const Scirt = ({ isSelected, image, handleClick }) => {
    return (
        <img
            className={isSelected ? 'selected-skirt' : 'skirt'}
            src={image}
            alt={'scirt'}
            onClick={handleClick}
        />
    );
};

Scirt.propTypes = {
    isSelected: PropTypes.bool.isRequired,
    image: PropTypes.string.isRequired,
    handleClick: PropTypes.func.isRequired,
};

export default Scirt;



