import { createSelector } from 'reselect';

const getCards = state => state.cards;
const getScirts = state => state.scirts;
export const getScore = state => state.gameState.score;

export const cardsSelector = createSelector(getCards, cards => cards);

export const scirtsSelector = createSelector(getScirts, scirts => scirts);

export const selectedScirtSelector = createSelector(
    getScirts,
    scirts => scirts.find(scirt => scirt.isSelected).image
);



