import {
    SELECT_SCIRT,
    CHECK_GAME,
    RESET_GAME,
    CHANGE_TIME,
    START_TIMER,
    STOP_TIMER,
    RESET_TIMER,
    INIT_CARDS,
    TURN_CARD,
    FETCH_SORTED_RESULTS,
    DELETE_RESULTS,
    CHECK_CARD,
} from '../constants';

export const selectScirt = id => ({
    type: SELECT_SCIRT,
    payload: id,
});

export const checkGame = cards => ({
    type: CHECK_GAME,
    payload: cards,
});

export const resetGame = () => ({
    type: RESET_GAME,
});

export const startTimer = func => ({
    type: START_TIMER,
    payload: func,
});

export const changeTime = () => ({
    type: CHANGE_TIME,
});

export const stopTimer = id => ({
    type: STOP_TIMER,
    payload: id,
});

export const resetTimer = () => ({
    type: RESET_TIMER,
});

export const initCards = () => ({
    type: INIT_CARDS,
});

export const turnCard = id => ({
    type: TURN_CARD,
    payload: id,
});

export const checkCard = () => ({
    type: CHECK_CARD,
});

export const fetchSortedResults = (name, email, time) => ({
    type: FETCH_SORTED_RESULTS,
    payload: {
        name,
        email,
        time,
    },
});

export const deleteResults = () => ({
    type: DELETE_RESULTS,
});


