import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { startTimer, changeTime, stopTimer } from './../../actions';

import Timer from './../presentational/Timer';

export class TimerContainer extends Component {
    componentDidMount() {
        const { startTimer, changeTime } = this.props;
        startTimer(changeTime);
    }

    componentWillUnmount() {
        const { stopTimer, timer } = this.props;
        stopTimer(timer.id);
    }

    render() {
        const { time } = this.props.timer;

        return <Timer time={time} />;
    }
}

TimerContainer.propTypes = {
    timer: PropTypes.object.isRequired,
    startTimer: PropTypes.func.isRequired,
    changeTime: PropTypes.func.isRequired,
    stopTimer: PropTypes.func.isRequired,
};

export default connect(
    state => ({ timer: state.timer }),
    {
        startTimer,
        changeTime,
        stopTimer,
    }
)(TimerContainer);



