import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { selectScirt } from '../../actions';

import Scirt from '../presentational/Scirt';

export class ScirtContainer extends Component {
    render() {
        const { selectScirt, isSelected, image } = this.props;

        return (
            <Scirt
                isSelected={isSelected}
                image={image}
                handleClick={selectScirt}
            />
        );
    }
}

ScirtContainer.propTypes = {
    selectScirt: PropTypes.func.isRequired,
};

export default connect(
    null,
    (dispatch, ownprops) => ({
        selectScirt: () => dispatch(selectScirt(ownprops.id)),
    })
)(ScirtContainer);



