import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { withRouter } from 'react-router-dom';
import { cardsSelector, selectedScirtSelector } from '../../selectors';
import { initCards, checkGame } from '../../actions';

import CardContainer from './CardContainer';

import './../../styles/GameField.css';

export class GameFieldContainer extends Component {
    componentDidMount() {
        this.props.initCards();
    }

    async componentDidUpdate() {
        const { checkGame, cards } = this.props;
        await checkGame(cards);

        const { isFinished, history } = this.props;
        isFinished && history.push('/results');
    }

    render() {
        const { cards, scirt } = this.props;

        return (
            <section className="game-field-container">
                {cards.map(card => (
                    <CardContainer
                        key={card.id}
                        id={card.id}
                        isTurned={card.isTurned}
                        isOut={card.isOut}
                        image={card.image}
                        scirt={scirt}
                    />
                ))}
            </section>
        );
    }
}

GameFieldContainer.propTypes = {
    cards: PropTypes.array.isRequired,
    isFinished: PropTypes.bool.isRequired,
    initCards: PropTypes.func.isRequired,
    checkGame: PropTypes.func.isRequired,
};

export default withRouter(
    connect(
        state => ({
            cards: cardsSelector(state),
            scirt: selectedScirtSelector(state),
            isFinished: state.gameState.isFinished,
        }),
        {
            initCards,
            checkGame,
        }
    )(GameFieldContainer)
);



