import React from 'react';
import PropTypes from 'prop-types';

import './../../styles/ScorePageButtons.css';
import './../../styles/Button.css';

const ScorePageButtons = ({ handleClickTryBtn, handleClickMenuBtn }) => {
    return (
        <div className="buttons-container">
            <button className="start-game-btn" onClick={handleClickTryBtn}>
                Try again!
            </button>
            <button className="start-game-btn" onClick={handleClickMenuBtn}>
                Main menu
            </button>
        </div>
    );
};

ScorePageButtons.propTypes = {
    handleClickTryBtn: PropTypes.func.isRequired,
    handleClickMenuBtn: PropTypes.func.isRequired,
};

export default ScorePageButtons;



