import { put, call } from 'redux-saga/effects';
import { delay } from 'redux-saga';

import { CHECK_CARD_START } from '../constants';

export function* checkCard() {
    yield call(delay, 500);
    yield put({
        type: CHECK_CARD_START
    });
};


