import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';

import RecordsTableRow from '../presentational/RecordsTableRow';

import './../../styles/RecordsTable.css';

export class RecordsTableContainer extends Component {
    render() {
        const { results, isError } = this.props;

        return (
            <table className="table-records">
                <thead>
                    <tr>
                        <th>User Name</th>
                        <th>Email</th>
                        <th>Score</th>
                    </tr>
                </thead>
                <tbody>
                    {isError
                        ? <tr><td colSpan='3'>Error while loading highscores, the server doesn't work...</td></tr>
                        : results.map(result => (
                              <RecordsTableRow
                                  key={result._id}
                                  username={result.username}
                                  email={result.email}
                                  score={result.score}
                              />
                          ))}
                </tbody>
            </table>
        );
    }
}

RecordsTableContainer.propTypes = {
    results: PropTypes.array,
};

export default connect(state => ({
    results: state.results.data,
    isError: state.results.isError,
}))(RecordsTableContainer);



