import { shuffle, times, uniqueId } from 'lodash';

import { INIT_CARDS, TURN_CARD, CHECK_CARD_START } from '../constants';

import image1 from './../assets/images/pair1.png';
import image2 from './../assets/images/pair2.png';
import image3 from './../assets/images/pair3.png';
import image4 from './../assets/images/pair4.png';
import image5 from './../assets/images/pair5.png';

const images = [image1, image2, image3, image4, image5];

const finishAnimation = cards => {
    return cards.map(card => {
        if (card.isAnimated) card.isAnimated = !card.isAnimated;
        return card;
    });
};

const pairCheck = cards => {
    const filteredCards = cards.filter(card => !card.isOut && card.isTurned);
    return filteredCards.length === 2;
};

const equalityCheck = cards => {
    const turnedCards = cards.filter(card => !card.isOut && card.isTurned);

    return turnedCards[0].image === turnedCards[1].image
        ? cards.map(card => {
              if (!card.isOut && card.isTurned) card.isOut = !card.isOut;
              return card;
          })
        : cards.map(card => {
              if (!card.isOut && card.isTurned) card.isTurned = !card.isTurned;
              return card;
          });
};

const cards = (state = [], action) => {
    switch (action.type) {
        case INIT_CARDS:
            const pairs = images.concat(images);
            const mixPairs = shuffle(pairs);

            return times(10, time => ({
                id: uniqueId(),
                isTurned: false,
                isOut: false,
                image: mixPairs[time],
                isAnimated: false,
            }));
        case TURN_CARD:
            return state.some(card => card.isAnimated)
                ? state
                : state.map(card => {
                      if (card.id === action.payload) {
                          card.isTurned = !card.isTurned;
                          card.isAnimated = !card.isAnimated;
                      }
                      return card;
                  });
        case CHECK_CARD_START:
            const updatedCards = finishAnimation(state);
            const isVerifiable = pairCheck(updatedCards);

            return isVerifiable ? equalityCheck(updatedCards) : updatedCards;
        default:
            return state;
    }
};

export default cards;


