import React from 'react';

import './../../styles/IntroductionHeader.css';

const IntroductionHeader = () => {
    return (
        <article>
            <h1>HELLO!!!</h1>
            <p>
                Find all pairs of cards as quickly as possible to break the
                record!
            </p>
        </article>
    );
};

export default IntroductionHeader;



