import {
    FETCH_RECORDS_SUCCESS,
    FETCH_RECORDS_ERROR,
    FETCH_RECORDS_REQUEST,
    DELETE_RESULTS,
} from '../constants';

const initialState = {
    data: null,
    isLoading: true,
    isError: false,
    error: null,
};

const results = (state = initialState, action) => {
    switch (action.type) {
        case FETCH_RECORDS_REQUEST:
            return {
                ...state,
                isLoading: true,
            };
        case FETCH_RECORDS_SUCCESS:
            return {
                ...state,
                data: action.payload,
                isLoading: false,
            };
        case FETCH_RECORDS_ERROR:
            return {
                ...state,
                isLoading: false,
                isError: true,
                error: action.payload,
            };
        case DELETE_RESULTS:
            return {
                ...state,
                data: null,
                isLoading: true,
                isError: false,
                error: null,
            };
        default:
            return state;
    }
};

export default results;


