import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
    fetchSortedResults,
    deleteResults,
} from '../../actions';

import ScorePageHeaderContainer from './ScorePageHeaderContainer';
import RecordsTableContainer from './RecordsTableContainer';
import ScorePageButtonsContainer from './ScorePageButtonsContainer';

import './../../styles/ScorePage.css';

export class ScorePageContainer extends Component {
    componentDidMount() {
        const {
            fetchSortedResults,
            time,
            userData,
        } = this.props;
        fetchSortedResults(userData.username, userData.email, time);
    }

    componentWillUnmount() {
        this.props.deleteResults();
    }

    render() {
        const { isLoading } = this.props.results;

        return isLoading ? (
            <div className="preloader">...Loading</div>
        ) : (
            <section className="user-result">
                <ScorePageHeaderContainer />
                <ScorePageButtonsContainer />
                <RecordsTableContainer />
            </section>
        );
    }
}

ScorePageContainer.propTypes = {
    results: PropTypes.object.isRequired,
    time: PropTypes.string.isRequired,
    userData: PropTypes.object.isRequired,
    fetchSortedResults: PropTypes.func.isRequired,
    deleteResults: PropTypes.func.isRequired,
};

export default connect(
    state => ({
        results: state.results,
        time: state.timer.time,
        userData: state.form.registration.values,
    }),
    {
        fetchSortedResults,
        deleteResults,
    }
)(ScorePageContainer);


