import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { turnCard, checkCard } from '../../actions';

import Card from '../presentational/Card';

export class CardContainer extends Component {
    handleClick = () => {
        const { turnCard, checkCard } = this.props;
        turnCard();
        checkCard();
    };

    render() {
        const { isTurned, isOut, image, scirt } = this.props;

        return (
            <Card
                isTurned={isTurned}
                isOut={isOut}
                image={image}
                scirt={scirt}
                handleClick={this.handleClick}
            />
        );
    }
}

CardContainer.propTypes = {
    id: PropTypes.string.isRequired,
    isTurned: PropTypes.bool.isRequired,
    isOut: PropTypes.bool.isRequired,
    image: PropTypes.string.isRequired,
    turnCard: PropTypes.func.isRequired,
    checkCard: PropTypes.func.isRequired,
};

export default connect(
    null,
    (dispatch, ownprops) => ({
        turnCard: () => dispatch(turnCard(ownprops.id)),
        checkCard: () => dispatch(checkCard()),
    })
)(CardContainer);



