import { takeLatest, all } from 'redux-saga/effects';

import {
    FETCH_SORTED_RESULTS,
    CHECK_CARD,
} from '../constants';

import { fetchSortedResults } from './dataSagas';
import { checkCard } from './cardsSaga';

export default function* rootSaga() {
    yield all([
        takeLatest(FETCH_SORTED_RESULTS, fetchSortedResults),
        takeLatest(CHECK_CARD, checkCard),
    ]);
}


